.dropdown {
  position: relative;
  display: inline-block;
  color: #b4b4b4; }
  .dropdown input[type=checkbox] {
    display: none; }
  .dropdown label {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    width: 122px;
    height: 36px;
    padding-top: 10px;
    border-radius: 4px;
    background-color: #fffefb;
    -webkit-box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
            box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    text-align: center; }
  .dropdown label::before {
    position: absolute;
    left: 0;
    top: 50%;
    height: 10px;
    width: 3px;
    background-color: #b4b4b4;
    content: "";
    -webkit-transform: translateX(10px) rotate(-45deg);
            transform: translateX(10px) rotate(-45deg);
    -webkit-transform-origin: left bottom;
            transform-origin: left bottom; }
  .dropdown label::after {
    position: absolute;
    left: 0;
    bottom: 0;
    height: 3px;
    width: 10px;
    background-color: #b4b4b4;
    content: "";
    -webkit-transform: translateX(10px) rotate(-45deg);
            transform: translateX(10px) rotate(-45deg);
    -webkit-transform-origin: left bottom;
            transform-origin: left bottom; }
  .dropdown ul {
    position: absolute;
    list-style: none;
    text-align: left;
    width: 100%;
    z-index: 1;
    display: none; }
  .dropdown ul li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 121.5px;
    height: 36px;
    background-color: #fffefb;
    color: #b4b4b4;
    cursor: pointer;
    width: 121.5px;
    height: 36px;
    border-radius: 4px;
    background-color: #fffefb;
    -webkit-box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
            box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1); }
  .dropdown ul li:hover {
    background-color: #7bae73;
    color: #fffefb; }
  .dropdown ul li a {
    text-align: center;
    color: inherit;
    text-decoration: none; }
  .dropdown input[type=checkbox]:checked ~ label {
    background-color: #fffefb;
    color: #e5e5e5; }
  .dropdown input[type=checkbox]:checked ~ ul {
    display: block; }
